@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300&display=swap);
:root{--clr-white: #ffff;--clr-primary: #f94444;--clr-gray: #263238;--clr-hover: #ffcccb;--font-size: 16px;--borer-radius: 0.2rem}.navbar{width:100vw;background-color:#ffff;background-color:var(--clr-white);margin:0;border-top:5px solid #f94444;border-top:5px solid var(--clr-primary);z-index:10}.navbar .logo{max-width:250px;max-height:70px}@media screen and (max-width: 550px){.navbar{font-size:1.5em;font-weight:bold;color:#000}.navbar .list-items{grid-gap:0;gap:0}.navbar .logo{display:none}}

:root{--clr-primary: #5930ff;--clr-primary-hover: #29e6a7;--clr-primary-dark: #039d69;--clr-gray100: #f9fbff;--clr-gray150: #f4f6fb;--clr-gray200: #eef1f6;--clr-gray300: #e1e5ee;--clr-gray400: #767b91;--clr-gray500: #4f546c;--radius: 0.2rem}.nav{position:-webkit-sticky;position:sticky;background-color:white;box-shadow:0px 0px 10px #e1e5ee;box-shadow:0px 0px 10px var(--clr-gray300);width:20vw}.nav__expand{width:2rem;height:2rem;fill:#767b91;fill:var(--clr-gray400);background-color:white;box-shadow:0px 0px 10px #e1e5ee;box-shadow:0px 0px 10px var(--clr-gray300);border-radius:50%;position:relative;right:-1rem;top:1rem;z-index:99;cursor:pointer;transform:rotatez(-180deg);transition:transform 200ms ease-in-out}.nav__expand:hover{fill:#4f546c;fill:var(--clr-gray500)}.nav__list{display:flex;flex-direction:column}.nav__listitem{list-style:none;transition:all 200ms ease-in;padding:1rem 1.5rem;border-left:6px solid transparent;cursor:pointer}.nav__listitem:first-child{margin-bottom:2rem}.nav__listitem:hover{background-color:#f4f6fb;background-color:var(--clr-gray150)}.nav__listitem a{display:flex;grid-gap:1rem;gap:1rem;align-items:center;color:#767b91;color:var(--clr-gray400);text-decoration:none;font-weight:500}.nav__listitem svg{fill:#767b91;fill:var(--clr-gray400);width:2rem}.nav__listitem-active{background-color:#eef1f6;background-color:var(--clr-gray200);border-left:6px solid #5930ff;border-left:6px solid var(--clr-primary)}.nav__listitem-active svg,.nav__listitem-active p{color:#5930ff;color:var(--clr-primary);fill:#5930ff;fill:var(--clr-primary)}.nav-closed .nav__expand{transform:rotatez(0deg)}.nav-closed .nav__listitem p{display:none}

@font-face{font-family:'Material Icons';font-style:normal;font-weight:400;src:url(https://fonts.gstatic.com/s/materialicons/v83/flUhRq6tzZclQEJ-Vdg-IuiaDsNc.woff2) format("woff2")}.material-icons{font-family:'Material Icons';font-weight:normal;font-style:normal;font-size:24px;line-height:1;letter-spacing:normal;text-transform:none;display:inline-block;white-space:nowrap;word-wrap:normal;direction:ltr;-webkit-font-feature-settings:'liga';-webkit-font-smoothing:antialiased}:root{--clr-white: #ffff;--clr-primary: #f94444;--clr-gray: #263238;--font-family: 'Roboto', sans-serif;--font-size: 16px;--borer-radius: 0.2rem}#wrapper-main{margin:2em}.header{display:flex;justify-content:center;align-items:center;margin-top:-1em}.header .mobile__logo__replacement{font-size:0em}.header #search{background-color:#ffff;background-color:var(--clr-white);border-radius:0.2rem;border-radius:var(--borer-radius);width:15em;height:2.5em;padding:auto;display:flex;align-items:center;color:#263238;color:var(--clr-gray)}.header .material-icons{padding:0 0.2em;font-size:1.5em}@media screen and (max-width: 550px){.header .mobile__logo__replacement{text-align:center;font-size:4em}.header .page__title{display:none}}

:root{--clr-white: #ffff;--clr-primary: #f94444;--clr-gray: #263238;--clr-hover: #ffcccb;--font-family: 'Roboto', sans-serif;--font-size: 16px;--borer-radius: 0.2rem}#wrapper-grid{display:grid;grid-template-columns:repeat(auto-fit, minmax(12em, 1fr));grid-gap:1.5em;margin-left:3em;margin-right:3em}.container{opacity:0.8;background-color:#ffff;background-color:var(--clr-white);border-radius:0.2rem;border-radius:var(--borer-radius);padding:1em;border-top:4px solid #f94444;border-top:4px solid var(--clr-primary)}.container:hover{z-index:100;opacity:1;transition:all 0.4s ease-in;background-color:#ffcccb;background-color:var(--clr-hover);color:#f94444;color:var(--clr-primary)}.container .opt-label{display:flex;flex-direction:column;align-items:center;justify-content:space-between;margin-bottom:0.2em;margin-top:0.5em;text-transform:uppercase;font-size:0.8em;letter-spacing:0.1em;margin-bottom:-1em;margin-top:0}.container .opt-label .opt-title{font-size:1.02em;margin-top:-0.75em;margin-bottom:1em}.container .opt-label .opt-title:hover{z-index:99}@media screen and (max-width: 550px){body{--font-size: 8px}#wrapper-grid{grid-template-columns:1fr 1fr}#wrapper-grid .container{max-width:20em;grid-gap:1em;gap:1em}#wrapper-grid .container:hover .opt-title{transition:all 0.4s ease-in;font-size:1.25em}.opt-title{font-size:1em;font-weight:bold;padding:0;margin:0}.category__image{max-width:8em}.header{display:flex;flex-direction:column;align-items:stretch;margin-bottom:1.5em}.header h1{margin:0.5em 0}.header #search{width:100%}}

:root{--clr-white: #ffff;--clr-primary: #f94444;--clr-gray: #263238;--font-size: 16px}.footer{background-color:#ffff;background-color:var(--clr-white);color:#263238;color:var(--clr-gray);padding:2.5rem;text-align:center;margin-top:5em;border-top:4px solid #f94444;border-top:4px solid var(--clr-primary)}@media (min-width: 64em){.footer .container{display:grid;grid-template-columns:2fr 1fr 1fr 3fr;grid-template-rows:1fr auto;grid-template-areas:'logo links1 links2 cta' 'social links1 links2 copyright';grid-gap:1rem;gap:1rem;justify-items:start}}.footer a{color:#263238;color:var(--clr-gray)}.footer__logo{display:inline-block}@media (max-width: 63.9375em){.footer__logo{margin-bottom:1.875rem}}@media (min-width: 64em){.footer__logo{grid-area:logo}}@media (max-width: 63.9375em){.footer__social{margin-bottom:1.875rem}}@media (min-width: 64em){.footer__social{grid-area:social;align-self:end}}.footer__social a{display:inline-block;height:1.25rem}.footer__social a svg path{transition:fill 150ms ease-in-out}.footer__social a:hover svg path{fill:#f94444;fill:var(--clr-primary)}.footer__social a:not(:last-child){margin-right:1rem}.footer__links{display:flex;flex-direction:column;align-items:center;font-size:0.9375rem}@media (min-width: 64em){.footer__links{justify-content:space-between;align-items:start}}@media (min-width: 64em){.footer__links.col1{grid-area:links1}}@media (max-width: 63.9375em){.footer__links.col2{margin-bottom:1.875rem}}@media (min-width: 64em){.footer__links.col2{grid-area:links2}}.footer__links a{line-height:2.25;transition:color 150ms ease-in-out}.footer__links a:hover{color:#f94444;color:var(--clr-primary)}@media (min-width: 64em){.footer__cta{grid-area:cta;text-align:right;justify-self:end}}@media (max-width: 63.9375em){.footer__cta a.button{margin-bottom:1.875rem}}.footer__copyright{font-size:0.8125rem;color:#263238;color:var(--clr-gray)}@media (min-width: 64em){.footer__copyright{grid-area:copyright;align-self:end;justify-self:end}}

